.button-primary {
  box-sizing: border-box;
  width: 100%;
  max-width: 200px;
  display: block;
  padding: 14px 12px 12px;
  line-height: 24px;
  font-weight: 500;
  color: $text-white;
  text-align: center;
  background-color: $bg-red;
  border: none;
  border-radius: 3px;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s;

  &:hover, &:focus {
    background-color: $bg-red-hover;
  }

  &:active {
    color: rgba($text-white, 0.3);
    background-color: $bg-red-active;
    box-shadow: inset 0px 3px 0px 0px rgba(0,1,1,0.09);
  }
}

.button-primary {
  &--green {
    background-color: $bg-green;

    &:hover, &:focus {
      background-color: $bg-green-hover;
    }

    &:active {
      background-color: $bg-green-active;
    }
  }

  &--yellow {
    background-color: $bg-yellow;

    &:hover, &:focus {
      background-color: $bg-yellow-hover;
    }

    &:active {
      background-color: $bg-yellow-active;
    }
  }

  &--carousel {
    max-width: 240px;
  }

  &--addvantages {
    max-width: 160px;
  }

  &--contacts {
    max-width: 219px;
  }

  &--popup-write {
    max-width: 260px;
  }
}

@media(max-width: $tablet-width){
  .button-primary {
    &--carousel {
      max-width: 200px;
    }
  }
}
