.input {
  width: 100%;
  max-width: 360px;
  padding: 14px 16px;
  box-sizing: border-box;
  background-color: $bg-white;
  border: 2px solid $color-border-input;
  border-radius: 3px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: rgba($text-dark-secondary, 0.5);
  transition: all 0.3s;

  &:hover {
    border: 2px solid $color-border-input-hover;
  }

  &:focus {
    border: 2px solid $color-border-input-focus;
    outline: none;
    color: rgba($text-dark-secondary, 1);
  }

  &--invalid {
    border: 2px solid $color-border-input-invalid;
    color: $text-red;
  }
}
