.button-secondary {
  box-sizing: border-box;
  width: 100%;
  max-width: 260px;
  display: block;
  padding: 14px 12px 12px;
  line-height: 24px;
  font-weight: 500;
  color: $text-black;
  text-align: center;
  background-color: $bg-grey;
  border: none;
  border-radius: 3px;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s;

  &:hover, &:focus {
    background-color: $bg-grey-hover;
  }

  &:active {
    color: rgba($text-black, 0.3);
    background-color: $bg-grey-active;
    box-shadow: inset 0px 3px 0px 0px rgba(0,1,1,0.09);
  }
}
