.popup-write {
  position: fixed;
  left: 50%;
  bottom: 80px;
  transform: translate(-50%, 0);
  z-index: 1;
  margin-top: 50px;
  margin-bottom: 100px;
  background-color: $bg-white;
  box-sizing: border-box;
  width: 100%;
  max-width: 960px;
  padding: 65px 100px 85px;
  box-shadow: 0px 20px 40px 0px rgba(0,1,1,0.75);
}

.popup-write::before {
  content: '';
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  display: block;
  width: 100vw;
  height: 100vh;
}

.popup-write__title {
  margin: 0;
  font-size: 45px;
  line-height: 45px;
  font-weight: 500;
  color: $text-black;
}

.popup-write__wrap {
  display: flex;
  flex-wrap: wrap;
  margin-top: 45px;
}

.popup-write__inner {
  flex-basis: 360px;
  &--name {
    margin-right: auto;
  }
  &--message {
    flex-basis: 100%;
  }
}

.popup-write__label {
  display: block;
  margin-bottom: 10px;
}

.popup-write__input {
  margin-bottom: 30px;
}

.popup-write__btn {
  margin-top: 40px;
}

.popup-write__btn-close {
  position: absolute;
  top: 80px;
  right: 90px;
}
