.pagination .paginationjs-pages .pagination-list li {
  border: none;
  border-radius: 0;
}

.pagination .pagination-list li {
  margin-right: 11px;
}

.pagination .pagination-list li > a {
  box-sizing: border-box;
  display: inline-block;
  min-width: 50px;
  height: auto;
  padding: 16px 5px;
  border: none;
  border-radius: 3px;
  background-color: $bg-grey;
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
  color: $text-black;
  text-transform: uppercase;
  text-align: center;
  transition: all 0.3s;

  &:hover, &:focus {
    background-color: $bg-grey-hover;
  }

  &:active {
    background-color: $bg-grey-active;
    box-shadow: inset 0px 3px 0px 0px rgba(0,1,1,0.09);
    color: rgba($text-black, 0.3);
  }
}

.pagination .pagination-list .pagination-prev > a,
.pagination .pagination-list .pagination-next > a {
  width: 260px;
}

.pagination .pagination-list .pagination-active > a {

  &, &:hover, &:focus, &:active {
    padding: 13px 5px;
    background-color: $bg-white; 
    border: 3px solid $border-grey;
    box-shadow: none;
    color: $text-black;
  }
}

@media(max-width: $mobile-width){
  .pagination {
    max-width: 320px;
  }

  .pagination .pagination-list li {
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }
  }

  .pagination .pagination-list .pagination-prev > a,
  .pagination .pagination-list .pagination-next > a {
    position: relative;
    width: auto;
    width: 50px;
    height: 50px;
    font-size: 0;
    line-height: 0;

    &::before {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      content: '';
      width: 20px;
      height: 20px;
      background-repeat: no-repeat;
      background-size: cover;
    }

    &:active::before {
      opacity: 0.3;
    }
  }

  .pagination .pagination-list .pagination-prev > a::before {
    background-image: url('../img/back.svg');
  }

  .pagination .pagination-list .pagination-next > a::before {
    background-image: url('../img/next.svg');
  }
}
