.nav-main {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.nav-main__btn {
  display: none;
}

.nav-main__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  max-width: 800px;
  margin: 0;
  padding: 0;
  padding-top: 5px;
  list-style: none;
}

.nav-main__item {
  margin-right: 46px;

  &:last-child {
    margin-left: auto;
    margin-right: 0;
  }
}

.nav-main__link {
  position: relative;
  display: inline-block;
  padding: 5px 0;
  text-decoration: none;
  font-weight: 500;
  line-height: 30px;
  color: $text-black;
  text-transform: uppercase;
  transition: all 0.3s;

  &:hover, &:focus {
    color: $text-red;
  }

  &:active {
    color: rgba($text-black, 0.3);
  }

  &--active:hover, &--active:focus, &--active:active {
    color: rgba($text-black, 1);
  }
}

.nav-main__link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  display: none;
  width: 100%;
  height: 2px;
  background-color: $color-link-line;
}

.nav-main__link--active::after {
  display: block;
}

.nav-main__link--basket {
  padding-left: 46px;
  background-image: url('../img/basket-icon.svg');
  background-position: 5px center;
  background-size: 15px 15px;
  background-repeat: no-repeat;
}

@media(max-width: $tablet-width){
  .nav-main {
    display: block;
    text-align: center;
  }

  .nav-main__list {
    margin: 0 auto;
    margin-top: 15px;
  }
}

@media(max-width: $mobile-width){
  .nav-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 320px;
    margin: 0 auto;
    text-align: left;
  }

  .nav-main__logo {
    margin: 0;
  }

  .nav-main__btn {
    display: inline-block;
  }

  .nav-main__list {
    display: none;
  }

  .nav-main__item {
    margin-right: 0;

    &:last-child {
      margin-left: 0;
      margin-right: 0;
    }
  }

  .nav-main__item + .nav-main__item {
    margin-top: 10px;
  }

  .nav-main__link {
    padding: 0;

    &--basket {
      padding-left: 30px;
    }
  }
}
