.carousel {
  margin-top: -19px;
  width: 100%;
  height: 430px;
  background-color: $bg-grey;
}

.carousel__item {
  position: relative;
  height: 430px;
  background-position: bottom right;
  background-repeat: no-repeat;

  &::before {
    position: absolute;
    bottom: 0;
    right: 0;
    content: '';
    display: block;
    background-repeat: no-repeat;
  }

  &--first::before {
    right: 31px;
    width: 698px;
    height: 413px;
    background-image: url('../img/slide-1.png');
    background-size: 698px 413px;

    @media(min-resolution: $resolution-dpi-2x), 
    (min-resolution: $resolution-dppx-2x) {
      background-image: url('../img/slide-1@2x.png');
    }

    @media(min-resolution: $resolution-dpi-3x), 
    (min-resolution: $resolution-dppx-3x) {
      background-image: url('../img/slide-1@3x.png');
    }
  }

  &--second::before {
    width: 663px;
    height: 430px;
    background-image: url(../img/slide-2.png);
    background-size: 663px 430px;

    @media(min-resolution: $resolution-dpi-2x), 
    (min-resolution: $resolution-dppx-2x) {
      background-image: url(../img/slide-2@2x.png);
    }

    @media(min-resolution: $resolution-dpi-3x), 
    (min-resolution: $resolution-dppx-3x) {
      background-image: url(../img/slide-2@3x.png);
    }
  }

  &--third::before {
    width: 759px;
    height: 411px;
    background-image: url(../img/slide-3.png);
    background-size: 759px 411px;

    @media(min-resolution: $resolution-dpi-2x), 
    (min-resolution: $resolution-dppx-2x) {
      background-image: url(../img/slide-3@2x.png);
    }

    @media(min-resolution: $resolution-dpi-3x), 
    (min-resolution: $resolution-dppx-3x) {
      background-image: url(../img/slide-3@3x.png);
    }
  }
}

.carousel__title {
  margin: 0;
  margin-top: 75px;
  font-size: 55px;
  line-height: 55px;
  font-weight: 500;
  color: $text-black;
}

.carousel__text {
  margin: 0;
  margin-top: 30px;
  line-height: 24px;
  color: $text-dark-primary;
}

.carousel__btn {
  margin-top: 37px;
}

.carousel__dots {
  position: absolute;
  left: 50%;
  bottom: 96px;
  transform: translate(-50%, 0);
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 88px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.carousel__dots button {
  position: relative;
  box-sizing: border-box;
  width: 18px;
  height: 18px;
  background-color: $bg-white;
  border: none;
  border-radius: 50%;
  font-size: 0;
  line-height: 0;
  cursor: pointer;
  transition: all 0.3s;
}

.carousel__dots .slick-active button::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: '';
  box-sizing: border-box;
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 2px solid #c1c1c1;
}

@media(max-width: $desktop-width){
  .carousel__item {
    &--first::before {
      width: 600px;
      height: 356px;
      background-size: 600px auto;
    }

    &--second::before {
      width: 580px;
      height: 377px;
      background-size: 580px auto;
    }

    &--third::before {
      width: 600px;
      height: 325px;
      background-size: 600px auto;
    }
  }
}

@media(max-width: $tablet-width){
  .carousel {
    margin: 0 auto;
    max-width: 800px;
    height: 330px;
  }

  .carousel__item {
    height: 330px;
  }

  .carousel__title {
    margin-top: 50px;
    font-size: 35px;
    line-height: 35px;
  }

  .carousel__text {
    margin-top: 20px;
    font-size: 15px;
  }

  .carousel__item {
    &::before {
      right: 0;
    }

    &--first::before {
      width: 450px;
      height: 266px;
      background-size: 450px auto;
    }

    &--second::before {
      width: 390px;
      height: 253px;
      background-size: 390px auto;
    }

    &--third::before {
      width: 415px;
      height: 222px;
      background-size: 415px auto;
    }
  }

  .carousel__dots {
    bottom: 75px;
  }
}
