.filter__inner {
  margin-bottom: 45px;
}

.filter__title {
  margin: 0;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  text-transform: uppercase;
  list-style: 0.01em;
  color: $text-black;
}

.filter__price .filter__title {
  margin-bottom: 50px;
}

.filter__list {
  margin: 0;
  margin-top: 14px;
  padding: 0;
  list-style: none;
}

.filter__item + .filter__item {
  margin-top: 16px;
}

.filter__toggle-btn {
  display: none;
  position: relative;
  padding: 0;
  padding-right: 30px;
  border: none;
  background-color: transparent;
  font-size: 20px;
  font-weight: 500;
  line-height: 30px;
  color: $text-black;
  transition: all 0.3s;
  cursor: pointer;
  opacity: 0.5;

  &:hover, &:focus {
    opacity: 1;
  }

  &:active {
    opacity: 0.3;
  }

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    display: block;
    width: 16px;
    height: 16px;
    background-image: url('../img/icon-down-arrow.svg');
    background-repeat: no-repeat;
    background-size: cover;
  }

  &--opened::after {
    background-image: url('../img/icon-up-arrow.svg');
  }
}

@media(max-width: $tablet-width){
  .filter {
    max-width: 800px;
  }

  .filter__toggle-btn {
    display: inline-block;
  }

  .filter__box {
    display: none;
  }

  .filter__wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 20px;
  }

  .filter__inner {
    margin-bottom: 15px;
  }

  .filter__price .filter__title {
    margin-bottom: 14px;
  }
}

@media(max-width: $mobile-width){
  .filter__wrap {
    display: block;
  }
}
