.works__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  padding-bottom: 40px;
  border-bottom: 2px solid $bg-grey;
  list-style: none;
}

.works__item {
  flex-basis: 25%;
  text-align: center;
}

.works__item + .works__item {
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    display: block;
    width: 2px;
    height: 52px;
    background-color: $bg-grey;
  }
}

.works__link {
  transition: all 0.3s;
  opacity: 0.2;
  &:hover, &:focus {
    opacity: 1;
  }

  &:active {
    opacity: 0.1;
  }
}

@media(max-width: $tablet-width){
  .works__list {
    max-width: 800px;
    margin: 0 auto;
    align-items: stretch;
  }

  .works__item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 50%;
  }

  .works__item:nth-child(odd)::before {
    display: none;
  }
}

@media(max-width: $mobile-width){
  .works__list {
    display: block;
    max-width: 320px;
    padding-bottom: 30px;
  }

  .works__item  + .works__item {
    margin-top: 30px;
  }

  .works__item + .works__item {
    padding-top: 30px;

    &::before {
      width: 52px;
      height: 2px;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
    }

    &:nth-child(odd)::before {
      display: block;
    }
  }
}
